@charset "utf-8";
/* CSS Document */

* {
  box-sizing: border-box;
}

:root {
  --purple: #7D314C;
  --yellow: #F7CA18;
  --blue: #1460AA;
  --darkblue: #1F3A93;
  --offwhite: #DADFE1;
  --darkgray: #2B2B2B;
  --lightgray: #bebebe;
}

body,td,th,p{
	font-family: Arial, Helvetica, sans-serif;
	font-size: 12pt;
	color: var(--darkgray);
	line-height: 1.5;
}
body {
	background-color: var(--offwhite);
	margin: 0;
	position: relative;
}
ul, li {
	font-family: Arial, Helvetica, sans-serif;
	font-size: 10pt;
	color: var(--darkgray);
}
a {
	color: var(--yellow);
	text-decoration: none;
}
a:hover, a:focus {
	color: var(--blue);
	text-decoration: none;
}
a img{
	border: none 0px var(--offwhite);
}
h1, h2, h3, h4, h5, h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 10px;
}
article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
	display: block;
}
#maincontent {
  background-color: var(--offwhite);
  min-height: 100%;
  min-width: 100%;
}
#footer {
  background-color: var(--darkgray);
  color: var(--offwhite);
  font-size: 0.8em;
  letter-spacing: 1px;
  padding: 4% 13%;
  text-align: center;
  text-transform: uppercase;
  max-width: 100%;
}
#footer a:focus {
  outline: 2px solid var(--yellow);
  padding: 2px;
}
/* ====================== Navigation ====================== */
nav {
  width: 100%;
  height: 80px;
  background-color: var(--darkgray);
  text-align: center;
}
nav h1 {
  margin: auto;
  padding-top: 1.5%;
}
nav h1 a {
  color: var(--yellow);
  font-size: 16pt;
  font-weight: 200;
  letter-spacing: 10px;
  text-transform: uppercase;
}
nav h1 a:focus {
  outline: 3px solid var(--blue);
  padding: 2px 4px;
}
#breadcrumb {
    padding: 10px 20px 16px;
    list-style: none;
    background-color: var(--lightgray);
    margin: 0;
    width: 100%;
}

/* Display list items side by side */
#breadcrumb li {
    display: inline;
    font-size: 1.1em;
}

/* Add a slash symbol (/) before/behind each list item */
#breadcrumb li+li:before {
    padding: 8px;
    color: var(--darkgray);
    content: "/\00a0";
}

/* Add a color to all links inside the list */
#breadcrumb li a {
    color: var(--darkblue);
    text-decoration: none;
}

/* Add a color on mouse-over */
#breadcrumb li a:hover {
    color: var(--purple);
    text-decoration: underline;
}
#breadcrumb li a:focus {
  outline: 2px solid var(--purple);
  padding: 2px;
}
/* ====================== Map ====================== */
#map-container {
  background: var(--blue) none repeat scroll 0 0;
  width: 100%;
  min-height: 350px;
  height: 60%;
}
#map {
  height: 100%;
  min-height: 350px;
  width: 100%;
  background-color: var(--offwhite);
}
/* ====================== Restaurant Filtering ====================== */
.filter-options {
  width: 100%;
  height: 35%;
  background-color: var(--blue);
}
.filter-options h2 {
  color: var(--offwhite);
  font-size: 1.7em;
  font-weight: normal;
  line-height: 1;
  margin: 0 10px;
  padding-top: 15px;
  text-align: center;
}
.filter-options select {
  background-color: var(--offwhite);
  border: 1px solid var(--offwhite);
  font-family: Arial,sans-serif;
  font-size: 1em;
  height: 50px;
  letter-spacing: 0;
  margin: 10px;
  padding: 0 10px;
  width: 43%;
}
#neighborhoods-select:focus, 
#cuisines-select:focus {
  outline: 4px solid var(--yellow);
}

/* ====================== Loading ================================= */
.loading {
  display: flex;
  justify-content: center;

}

.loading-img {
  margin: 2% 4%;
}

.loading-text {
  font-size: 1.75em;
}

.hidden {
  display: none;
}

/* ====================== Restaurant Listing ====================== */
#restaurants-list {
  background-color: var(--lightgray);
  list-style: outside none none;
  margin: 0;
  padding: 30px 15px 60px;
  text-align: center;
}
#restaurants-list li {
  background-color: var(--offwhite);
  border: 2px solid var(--offwhite);
  font-family: Arial,sans-serif;
  margin: 2% auto 4%;
  min-height: 380px;
  padding: 0 10px 25px;
  text-align: left;
  width: 90%;
}

#restaurants-list .restaurant-img {
  background-color: var(--offwhite);
  display: block;
  margin-top: 0.5rem;
  max-width: 100%;
  min-width: 100%;
  min-height: 200px;
}
#restaurants-list li h3 {
  color: var(--purple);
  font-family: Arial,sans-serif;
  font-size: 2.3em;
  font-weight: 300;
  letter-spacing: 0;
  line-height: 1.3;
  text-transform: uppercase;
}

.favButton {
  background: none;
  border: none;
  position: relative;
  height: 3.5em;
  width: 100%;
  margin: 5% auto;
  padding: 3px;
}

.favorite {
  position: absolute;
  left: 0;
  top: 0;
  width: 2.5em;
  transition: opacity 0.55s ease-out;
}

.hide {
  opacity: 0;
}

#restaurants-list p {
  margin: 0;
  font-size: 1.5em;
}
#restaurants-list li a {
  background-color: var(--purple);
  color: var(--offwhite);
  display: inline-block;
  font-size: 1.4em;
  margin: 15px 0 0;
  padding: 10px 15px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  box-shadow: 3px 3px 3px var(--darkgray);
  transition: all 0.3s ease-in-out;
}

#restaurants-list li a:focus,
#restaurants-list li a:hover {
  background-color: var(--blue);
  outline: 0;
  box-shadow: 6px 6px 5px var(--darkgray);
}

/* ====================== Restaurant Details ====================== */
.inside header {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.inside #maincontent {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-width: 100%;
}

.inside #map-container {
  background: var(--blue) none repeat scroll 0 0;
  width: 90%;
  min-height: 350px;
}
.inside #map {
  background-color: var(--offwhite);
  height: 100%;
  width: 100%;
  min-height: 350px;
}
.inside #footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}
#restaurant-name {
  color: var(--purple);
  font-family: Arial,sans-serif;
  font-size: 2.6em;
  font-weight: 300;
  letter-spacing: 0;
  margin: 15px 0 30px;
  text-transform: uppercase;
  line-height: 1.1;
  width: 100%;
  text-align: center;
}
#restaurant-img {
	width: 80%;
  margin: 0 10%;
}
#restaurant-address {
  font-size: 1.5em;
  margin: 10px 0;
  padding: 0 8%;
  width: 100%;
  text-align: center;
}
#restaurant-cuisine {
  background-color: var(--darkgray);
  color: var(--offwhite);
  font-size: 1.7em;
  font-weight: 300;
  letter-spacing: 10px;
  margin: 0 0 20px;
  padding: 2px 0;
  text-align: center;
  text-transform: uppercase;
	width: 100%;
}
#restaurant-hours td {
  color: var(--darkgray);
  font-size: 1.2em;
  padding: 1% 3%;
  border-bottom: 1px solid var(--darkgray);
}
#restaurant-hours {
  width: 90%;
  margin: 0 5%;
}
#restaurant-container, #reviews-container {
  padding: 140px 0 30px;
}

#restaurant-container .favorite {
  left: 50%;
}

#reviews-container {
  padding: 30px 0 80px;
}
#reviews-container h3 {
  color: var(--purple);
  font-size: 2.3em;
  font-weight: 300;
  letter-spacing: -1px;
  padding-bottom: 1pt;
  text-align: center;
}
#reviews-list {
  margin: 0;
  padding: 0;
}
#reviews-list li {
  background-color: var(--offwhite);
  border: 2px solid var(--blue);
  display: block;
  list-style-type: none;
  margin: 0 5% 5%;
  overflow: hidden;
  padding: 15px;
  position: relative;
  width: 90%;
}
#reviews-list li p {
  margin: 0 0 10px;
}

/* ====================== Add Review Form ====================== */
#addReview {
  background-color: var(--lightgray);
  color: var(--darkgray);
  border: 3px solid var(--blue);
  width: 90%;
  margin: 0 auto;
  padding: 2% 0 4%;
}

#addReview h4 {
  text-align: center;
  color: var(--purple);
}

.formDivider {
  padding: 2% 8%;
  margin: 1% 2%;
}

.formDivider label {
  width: 60%;
  display: block;
  margin-left: 3vw;
}

.formDivider input {
  width: 90%;
  vertical-align: top;
  border: 1px solid var(--darkgray);
  border-radius: 5px;
  padding: 2%;
  margin-left: 3vw;
}

.formDivider textarea {
  width: 90%;
  height: 8em;
  margin-bottom: 2%;
  border: 1px solid var(--darkgray);
  border-radius: 5px;
  margin-left: 3vw;
}

#submitReview {
  width: 70%;
  background: var(--blue);
  color: var(--offwhite);
  height: 3em;
  margin: 0 12%;
  border-radius: 5px;
  font-size: 1.2rem;
  border: 2px solid var(--offwhite);
  box-shadow: 3px 3px 3px var(--darkgray);
  transition: all 0.3s ease-in-out;
}

#submitReview:hover {
  background-color: var(--purple);
  box-shadow: 6px 6px 5px var(--darkgray);
}

/* ====================== Breakpoints ====================== */
@media screen and (min-width: 475px) {
  nav {
    height: 50px;
  }
}


@media screen and (min-width: 600px) {
  #map-container {
    height: 65vh;
  }
  .filter-options select {
    margin: 2% 5%;
    width: 38%;
  }
  #restaurants-list {
    display: flex;
    flex-flow: wrap;
  }
  #restaurants-list li h3 {
    font-size: 1.7em;
  }
  #restaurants-list li {
    width: 47%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 3% 4%;
  }
  #restaurants-list li a {
    font-size: 1.1em;
    width: 75%;
  }
  #restaurants-list p {
    font-size: 1.3em;
  }

  #restaurant-address {
    font-size: 1.4em;
    width: 100%;
  }

  #restaurant-hours td {
    font-size: 1.4em;
    padding: 1% 3%;
  }
  #restaurant-hours {
    width: 80%;
    margin: 0 10%;
  }

  #submitReview {
    width: 30%;
    margin: 0 25% 2% 35%;
  }
}

@media screen and (min-width: 800px) {
  .filter-options {
    height: 25%;
    display: flex;
    align-items: baseline;
  }
  .filter-options h2 {
    text-align: left;
    padding-left: 3%;
  }
  .filter-options select {
    font-size: 1.2em;
    height: 40px;
    width: 25%;
  }

  #restaurants-list {
    padding: 20px 15px;
  }
  #restaurants-list li {
    margin: 2% auto;
  }
  #restaurants-list li h3 {
    font-size: 1.7em;
  }
  #restaurants-list p {
    font-size: 1.3em;
  }
  #restaurants-list li a {
    font-size: 1.3em;
    width: 65%;
  }

  #footer {
    font-size: 1em;
    padding: 3% 10% 2%;
  }
  #breadcrumb li {
      font-size: 1.3em;
  }

  .inside #maincontent {
    display: flex;
    flex-flow: wrap;
    justify-content: space-around;
  }
  #restaurant-container {
    width: 50%;
    margin: -45% 0 5vh;
    padding: 0;
  }
  #restaurant-name {
    font-size: 2em;
    padding: 0 5%;
  }
  #restaurant-cuisine {
    width: 90%;
    margin: 1% 5%;
  }

  #reviews-container {
    width: 50%;
    margin: 2vh 0 10vh;
    padding: 0;
  }
  #reviews-container h3 {
    font-size: 2em;
  }

  .inside #map-container {
    order: -1;
    margin-top: 15vh;
    margin-bottom: 10%;
  }

}

@media screen and (min-width: 1000px) {
  nav {
    height: 75px;
  }

  nav h1 a {
    font-size: 1.5em;
  }

  #restaurants-list li {
    width: 32%;
  }
  #restaurants-list li a {
    font-size: 1em;
    width: 60%;
  }

  .inside #map-container {
    border: 0;
    width: 45%;
    margin-top: 0;
    height: 85vh;
  }

  #restaurant-container {
    margin-top: 20vh;
  }

  #reviews-container {
    width: 80%;
  }

  #reviews-list li p {
    font-size: 1.2em;
  }
}

@media screen and (min-width: 1200px) {
  html {
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    background-color: var(--offwhite);
  }

  header {
    max-width: 1200px;
  }

  .filter-options h2 {
    font-size: 1.6em;
  }
}
